<template lang="html">
  <div class="orderDetail">
    <el-breadcrumb separator="/" class="app-breadcrumb">
      <el-breadcrumb-item :to="{ name: 'OrderList' }">订单列表</el-breadcrumb-item>
      <el-breadcrumb-item>订单详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form disabled :model="formData" label-width="100px" class="app-form">
      <el-form-item label="订单号：">{{formData.orderId}}</el-form-item>
      <el-form-item label="订单时间：">{{formData.orderTime}}</el-form-item>
      <el-form-item label="产品名称：">{{formData.productName}}</el-form-item>
      <el-form-item label="员工姓名：">{{formData.staffName}}</el-form-item>
      <el-form-item label="员工手机：">{{formData.staffPhone}}</el-form-item>
      <el-form-item label="客户姓名：">{{formData.customerName}}</el-form-item>
      <el-form-item label="客户手机：">{{formData.customerPhone}}</el-form-item>
      <el-form-item label="配送员姓名：">{{formData.delivererName}}</el-form-item>
      <el-form-item label="配送员手机：">{{formData.delivererPhone}}</el-form-item>
      <el-form-item label="配送地址：">{{formData.delivererAddress}}</el-form-item>
      <el-form-item label="配送时间：">{{formData.delivererTime}}</el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
.orderDetail {
  padding: 0 40px;

  .app {
    &-breadcrumb {
      margin-bottom: 40px;
    }

    &-form {
      width: 600px;
    }
  }
}
</style>

<script>
import { mapState, mapActions } from 'vuex'

export default {

  name: 'OrderDetail',
  computed: {
    ...mapState(['orderDetail']),
    formData () {
      return this.orderDetail
    },
  },
  methods: {
    ...mapActions(['getOrderDetail']),
    toOrderList () {
      this.$router.push({
        name: 'OrderList',
      })
    },
  },
  created () {
    const { query } = this.$route
    const { orderId } = query

    if (orderId) {
      this.getOrderDetail({
        orderId,
      })
    }
  },

}
</script>
